                                                    <!-- <a id="aParentID" :class="{ 'parentwithchild newtimetrack d-inline-block parent-childrow expandchild': data.trackLinkedId > 0, 'd-none': data.trackLinkedId <= 0 }" href="javascript:;" ><i class="fa fa-chevron-down" 
                                                        ></i></a> -->
                                                    <div class="d-inline-block">
                                                        <a title="" value="-1" style="cursor:default;" class="text-dark" href="#">
                                                            <span class="d-block font-14 font-weight-medium">{{ data.projectName }} :{{ data.taskName }}</span>
                                                             <span class="text-dark d-block font-12">{{ data.trackDescription }}</span>  </a>
                                                    </div>
                                                    
                                                    
                                                </td>

                                                <td style="max-width:140px">
                                                    <div class="dottedbod_rt w-100 row">
                                                        <div class="float-left tagspanel" >
