                                                            <span v-for="(tags, tagIndex) of arrayConvert(data.tag_name)" :key="tagIndex" :class="tagIndex > 1 || showAllTags ? 'd-none' : ''">{{tags}}</span>

                                                            <!-- Display "2 More" link only if there are more than two tags -->
                                                            <a v-if="data.tag_name != null && !showAllTags" @click="showAllTags = true" class="" href="#">2 More</a>

                                                           <!-- Display all spans if the user has clicked the "2 More" link -->
                                                            <span v-for="(tags, tagIndex) of arrayConvert(data.tag_name)" :key="tagIndex" v-show="showAllTags">{{tags}}</span>
                                                             
