                                                        <div class="float-left">
                                                            <i :class="{ 'tt_lisitng_icons ico_listdollar float-left': (data.billableMark == '$'), 'tt_lisitng_icons ico_listdollar-o float-left': (data.billableMark != '$') }"></i>
                                                        </div>
                                                        <div class="float-left">
                                                    
                                                            <i :class="{'tt_lisitng_icons ico_listcup1 float-left':(data.trackType=='Break'),'tt_lisitng_icons ico_listcup2 float-left':(data.trackType!='Break')}"></i>
                                                        </div>
