</template>
<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props:{
 list:{
    type:Array
 }
    },
    data(){
return{
    showAllTags: false,
}
    },
    created(){
console.log("checkkk",this.list)
},
methods:{
