<template>

    <div>
        <AddManualTimer v-if="addManualTimeOn" v-on:addManualTime="addManualTimeOn=false"></AddManualTimer>
        <Timetrack v-else-if="!addManualTimeOn && timerStartFromList" v-on:addManualTime="addManualTimeOn=true"></Timetrack>
        <Activities></Activities>
        <TimeTrackListing v-on:timerClose="changeTimer" v-on:timerOpen="changeTimer"></TimeTrackListing>
    </div>
</template>
<script>
import { defineComponent} from '@vue/composition-api'
import Timetrack from "../../components/TimeTrack/AddTimer.vue"
import AddManualTimer from "../../components/TimeTrack/AddManualTimer.vue"
import TimeTrackListing from "../../components/TimeTrack/TimeTrackListing.vue"
import  Activities from "../../components/TimeTrack/Activities.vue"
export default defineComponent({
    components: {
        Timetrack,
        TimeTrackListing,
        Activities,
        AddManualTimer
    },
    data(){
        return{
            addManualTimeOn:false,
            timerStartFromList:true
        }
    },
    methods: {
        changeTimer(){
            this.timerStartFromList = this.timerStartFromList =!this.timerStartFromList
        }
    }
})
</script>
